    --font-main: 'Courier New', monospace;
    --font-title: 'Orbitron', sans-serif;
}

html, body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    font-family: var(--font-main);
    background-color: var(--dark-bg);
    color: var(--neon-blue);
}

/* --- WEBGL CONTAINER --- */
#webgl-container {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
}

#webgl-container canvas {
    display: block;
    width: 100%;
    height: 100%;
}

/* --- LOADING SCREEN --- */
#loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);
