}

.glitch-text {
    font-size: 4rem;
    font-weight: bold;
    color: var(--neon-pink);
    text-shadow:
        0 0 10px var(--neon-pink),
        0 0 20px var(--neon-pink),
        0 0 30px var(--neon-pink);
    position: relative;
    animation: glitch-animation 2s infinite;
    margin-bottom: 2rem;
