            0 0 10px var(--neon-pink),
            0 0 20px var(--neon-pink);
    }
    75% {
        text-shadow:
            -2px -2px 0 var(--neon-blue),
            2px 2px 0 var(--electric-yellow);
    }
    100% {
        text-shadow:
            0 0 10px var(--neon-pink),
            0 0 20px var(--neon-pink);
    }
