eAAe;IACf,YAAY;IACZ,SAAS;IACT,4CAA4C;IAC5C,wBAAwB;IACxB,oCAAoC;IACpC,mBAAmB;IACnB,sBAAsB;IACtB,2BAA2B;IAC3B,2CAA2C;IAC3C,kBAAkB;IAClB,aAAa;IACb,UAAU;IACV,iDAAiD;IACjD,oBAAoB;AACxB;;AAEA;IACI,UAAU;IACV,yCAAyC;AAC7C;;AAEA;IACI,UAAU;IACV,4CAA4C;AAChD;;AAEA;IACI,uBAAuB;IACvB,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;IAChB,qCAAqC;AACzC;;AAEA;IACI,6BAA6B;IAC7B,iBAAiB;IACjB,iBAAiB;IACjB,4CAA4C;AAChD;;AAEA;IACI;QACI,UAAU;QACV,oBAAoB;IACxB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,iBAAiB;IACrB;AACJ;;AAEA;IACI;QACI,UAAU;QACV,oBAAoB;IACxB;;IAEA;QACI,iBAAiB;QACjB,gBAAgB;IACpB;;IAEA;QACI,eAAe;IACnB;AACJ","sourcesContent":["/* ===== CYBERPUNK 3D RESUME - MAIN STYLES ===== */\r\n\r\n/* --- RESET & BASE --- */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\n:root {\r\n    /* Cyberpunk Color Palette */\r\n    --neon-pink: #FF00FF;\r\n    --neon-blue: #00FFFF;\r\n    --neon-purple: #9D00FF;\r\n    --electric-yellow: #FFFF00;\r\n    --metal-silver: #C0C0C0;\r\n    --dark-bg: #0a0a0a;\r\n    --dark-gray: #1a1a1a;\r\n    --light-gray: #2a2a2a;\r\n\r\n    /* Glitch Colors */\r\n    --glitch-color-1: #FF00FF;\r\n    --glitch-color-2: #00FFFF;\r\n    --glitch-color-3: #FFFF00;\r\n\r\n    /* UI */\r\n    --ui-bg: rgba(10, 10, 10, 0.85);\r\n    --ui-border: rgba(255, 0, 255, 0.3);\r\n    --ui-glow: 0 0 10px var(--neon-pink);\r\n\r\n    /* Typography */\r\n    --font-main: 'Courier New', monospace;\r\n    --font-title: 'Orbitron', sans-serif;\r\n}\r\n\r\nhtml, body {\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow: hidden;\r\n    font-family: var(--font-main);\r\n    background-color: var(--dark-bg);\r\n    color: var(--neon-blue);\r\n}\r\n\r\n/* --- WEBGL CONTAINER --- */\r\n#webgl-container {\r\n    width: 100%;\r\n    height: 100%;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 1;\r\n}\r\n\r\n#webgl-container canvas {\r\n    display: block;\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n/* --- LOADING SCREEN --- */\r\n#loading-screen {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: linear-gradient(135deg, #0a0a0a 0%, #1a0a1a 100%);\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    z-index: 9999;\r\n    transition: opacity 0.5s ease-out;\r\n}\r\n\r\n#loading-screen.fade-out {\r\n    opacity: 0;\r\n    pointer-events: none;\r\n}\r\n\r\n.loading-content {\r\n    text-align: center;\r\n}\r\n\r\n.glitch-text {\r\n    font-size: 4rem;\r\n    font-weight: bold;\r\n    color: var(--neon-pink);\r\n    text-shadow:\r\n        0 0 10px var(--neon-pink),\r\n        0 0 20px var(--neon-pink),\r\n        0 0 30px var(--neon-pink);\r\n    position: relative;\r\n    animation: glitch-animation 2s infinite;\r\n    margin-bottom: 2rem;\r\n}\r\n\r\n@keyframes glitch-animation {\r\n    0% {\r\n        text-shadow:\r\n            0 0 10px var(--neon-pink),\r\n            0 0 20px var(--neon-pink);\r\n    }\r\n    25% {\r\n        text-shadow:\r\n            2px 2px 0 var(--neon-blue),\r\n            -2px -2px 0 var(--electric-yellow);\r\n    }\r\n    50% {\r\n        text-shadow:\r\n            0 0 10px var(--neon-pink),\r\n            0 0 20px var(--neon-pink);\r\n    }\r\n    75% {\r\n        text-shadow:\r\n            -2px -2px 0 var(--neon-blue),\r\n            2px 2px 0 var(--electric-yellow);\r\n    }\r\n    100% {\r\n        text-shadow:\r\n            0 0 10px var(--neon-pink),\r\n            0 0 20px var(--neon-pink);\r\n    }\r\n}\r\n\r\n.loading-bar-container {\r\n    width: 300px;\r\n    height: 4px;\r\n    background: var(--dark-gray);\r\n    border: 1px solid var(--neon-pink);\r\n    margin: 2rem auto;\r\n    position: relative;\r\n    overflow: hidden;\r\n}\r\n\r\n.loading-bar {\r\n    width: 0%;\r\n    height: 100%;\r\n    background: linear-gradient(90deg, var(--neon-pink), var(--neon-blue), var(--neon-pink));\r\n    background-size: 200% 100%;\r\n    animation: loading-gradient 1.5s ease infinite;\r\n    box-shadow: 0 0 10px var(--neon-pink);\r\n    transition: width 0.3s ease;\r\n}\r\n\r\n@keyframes loading-gradient {\r\n    0% { background-position: 0% 50%; }\r\n    50% { background-position: 100% 50%; }\r\n    100% { background-position: 0% 50%; }\r\n}\r\n\r\n.loading-text, .loading-percentage {\r\n    color: var(--neon-blue);\r\n    font-size: 0.9rem;\r\n    margin-top: 0.5rem;\r\n    text-transform: uppercase;\r\n    letter-spacing: 2px;\r\n}\r\n\r\n/* --- UI OVERLAY --- */\r\n#ui-overlay {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    pointer-events: none;\r\n    z-index: 10;\r\n}\r\n\r\n#ui-overlay > * {\r\n    pointer-events: auto;\r\n}\r\n\r\n/* --- NAVIGATION MENU --- */\r\n#main-nav {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 280px;\r\n    height: 100%;\r\n    background: var(--ui-bg);\r\n    backdrop-filter: blur(10px);\r\n    border-right: 2px solid var(--neon-pink);\r\n    box-shadow: 0 0 20px rgba(255, 0, 255, 0.3);\r\n    transform: translateX(0);\r\n    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n    padding: 2rem 0;\r\n}\r\n\r\n#main-nav.nav-hidden {\r\n    transform: translateX(-100%);\r\n}\r\n\r\n.nav-logo {\r\n    font-size: 1.5rem;\r\n    font-weight: bold;\r\n    color: var(--neon-pink);\r\n    text-align: center;\r\n    margin-bottom: 2rem;\r\n    text-shadow: 0 0 10px var(--neon-pink);\r\n}\r\n\r\n.nav-items {\r\n    list-style: none;\r\n}\r\n\r\n.nav-items li {\r\n    padding: 1rem 2rem;\r\n    cursor: pointer;\r\n    transition: all 0.3s ease;\r\n    border-left: 3px solid transparent;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 1rem;\r\n}\r\n\r\n.nav-items li:hover {\r\n    background: rgba(255, 0, 255, 0.1);\r\n    border-left-color: var(--neon-pink);\r\n    color: var(--neon-pink);\r\n    text-shadow: 0 0 5px var(--neon-pink);\r\n}\r\n\r\n.nav-items li.active {\r\n    background: rgba(255, 0, 255, 0.2);\r\n    border-left-color: var(--neon-blue);\r\n    color: var(--neon-blue);\r\n}\r\n\r\n.nav-icon {\r\n    font-size: 1.2rem;\r\n}\r\n\r\n/* --- MENU TOGGLE BUTTON --- */\r\n#menu-toggle {\r\n    position: fixed;\r\n    top: 2rem;\r\n    left: 2rem;\r\n    width: 50px;\r\n    height: 50px;\r\n    background: var(--ui-bg);\r\n    border: 2px solid var(--neon-pink);\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 5px;\r\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n    z-index: 100;\r\n}\r\n\r\n/* Move button when menu is open */\r\n#menu-toggle.active {\r\n    left: calc(280px + 1rem);\r\n}\r\n\r\n#menu-toggle:hover {\r\n    box-shadow: 0 0 15px var(--neon-pink);\r\n    transform: scale(1.05);\r\n}\r\n\r\n#menu-toggle span {\r\n    width: 25px;\r\n    height: 2px;\r\n    background: var(--neon-pink);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n#menu-toggle.active span:nth-child(1) {\r\n    transform: rotate(45deg) translate(5px, 5px);\r\n}\r\n\r\n#menu-toggle.active span:nth-child(2) {\r\n    opacity: 0;\r\n}\r\n\r\n#menu-toggle.active span:nth-child(3) {\r\n    transform: rotate(-45deg) translate(7px, -6px);\r\n}\r\n\r\n/* --- SETTINGS PANEL --- */\r\n#settings-panel {\r\n    position: fixed;\r\n    top: 2rem;\r\n    right: 2rem;\r\n    z-index: 100;\r\n}\r\n\r\n.icon-button {\r\n    width: 50px;\r\n    height: 50px;\r\n    background: var(--ui-bg);\r\n    border: 2px solid var(--neon-blue);\r\n    border-radius: 5px;\r\n    cursor: pointer;\r\n    font-size: 1.5rem;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.icon-button:hover {\r\n    box-shadow: 0 0 15px var(--neon-blue);\r\n    transform: rotate(90deg);\r\n}\r\n\r\n#settings-content {\r\n    position: absolute;\r\n    top: 60px;\r\n    right: 0;\r\n    background: var(--ui-bg);\r\n    border: 2px solid var(--neon-blue);\r\n    border-radius: 5px;\r\n    padding: 1.5rem;\r\n    min-width: 250px;\r\n    backdrop-filter: blur(10px);\r\n    box-shadow: 0 0 20px rgba(0, 255, 255, 0.3);\r\n}\r\n\r\n#settings-content.hidden {\r\n    display: none;\r\n}\r\n\r\n.setting-item {\r\n    margin-bottom: 1rem;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    gap: 1rem;\r\n}\r\n\r\n.setting-item label {\r\n    color: var(--neon-blue);\r\n    font-size: 0.9rem;\r\n}\r\n\r\n.setting-item select,\r\n.setting-item input[type=\"range\"] {\r\n    background: var(--dark-gray);\r\n    color: var(--neon-blue);\r\n    border: 1px solid var(--neon-blue);\r\n    padding: 0.3rem;\r\n    border-radius: 3px;\r\n}\r\n\r\n/* --- MINI MAP (REMOVED - User feedback: unclear purpose) --- */\r\n#mini-map {\r\n    display: none;\r\n}\r\n\r\n#mini-map-canvas {\r\n    display: none;\r\n}\r\n\r\n/* --- INSTRUCTIONS (REMOVED - User feedback: WASD/SPACE controls don't exist) --- */\r\n#instructions {\r\n    display: none;\r\n}\r\n\r\n#instructions p {\r\n    display: none;\r\n}\r\n\r\n/* --- SCENE INFO --- */\r\n#scene-info {\r\n    position: fixed;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    text-align: center;\r\n    pointer-events: none;\r\n    opacity: 0;\r\n    transition: opacity 0.5s ease;\r\n    z-index: 150;\r\n}\r\n\r\n#scene-info.show {\r\n    opacity: 1;\r\n}\r\n\r\n#scene-title {\r\n    font-size: 3rem;\r\n    color: var(--neon-pink);\r\n    text-shadow: 0 0 20px var(--neon-pink);\r\n    margin-bottom: 1rem;\r\n    text-transform: uppercase;\r\n}\r\n\r\n#scene-description {\r\n    font-size: 1.2rem;\r\n    color: var(--neon-blue);\r\n}\r\n\r\n/* --- BLACK SCREEN TRANSITION (Cinematic Slide Effect) --- */\r\n#black-transition {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: #000000;\r\n    opacity: 0;\r\n    pointer-events: none;\r\n    z-index: 999;\r\n    transition: opacity 0.6s ease-in-out;\r\n}\r\n\r\n#black-transition.active {\r\n    opacity: 1;\r\n}\r\n\r\n/* --- 3D CONTENT DISPLAY SYSTEM (Subtitle Style) --- */\r\n#content-display {\r\n    position: fixed;\r\n    bottom: 15%;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    width: 80%;\r\n    max-width: 900px;\r\n    text-align: center;\r\n    pointer-events: none;\r\n    z-index: 100;\r\n}\r\n\r\n.content-section {\r\n    opacity: 0;\r\n    transform: translateY(20px);\r\n    transition: opacity 0.8s ease-out, transform 0.8s ease-out;\r\n    margin-bottom: 1.5rem;\r\n}\r\n\r\n.content-section.show {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n}\r\n\r\n.content-section.fade-out {\r\n    opacity: 0;\r\n    transform: translateY(-20px);\r\n}\r\n\r\n#content-title h1 {\r\n    font-size: 2.5rem;\r\n    color: var(--neon-pink);\r\n    text-shadow:\r\n        0 0 10px var(--neon-pink),\r\n        0 0 20px var(--neon-pink),\r\n        0 0 30px rgba(255, 0, 255, 0.5);\r\n    margin: 0;\r\n    padding: 0;\r\n    font-weight: bold;\r\n    text-transform: uppercase;\r\n    letter-spacing: 2px;\r\n}\r\n\r\n#content-text p {\r\n    font-size: 1.1rem;\r\n    line-height: 1.8;\r\n    color: var(--neon-blue);\r\n    text-shadow:\r\n        0 0 5px var(--neon-blue),\r\n        0 0 10px rgba(0, 255, 255, 0.3);\r\n    margin: 0;\r\n    padding: 1rem 2rem;\r\n    background: rgba(0, 0, 0, 0.3);\r\n    backdrop-filter: blur(5px);\r\n    border-left: 3px solid var(--neon-blue);\r\n    border-right: 3px solid var(--neon-blue);\r\n    white-space: pre-wrap;\r\n}\r\n\r\n#content-hint {\r\n    position: absolute;\r\n    bottom: -3rem;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    opacity: 0;\r\n    transition: opacity 0.5s ease;\r\n}\r\n\r\n#content-hint.show {\r\n    opacity: 1;\r\n    animation: hint-bounce 2s ease-in-out infinite;\r\n}\r\n\r\n#content-hint p {\r\n    font-size: 0.9rem;\r\n    color: var(--electric-yellow);\r\n    text-shadow: 0 0 5px var(--electric-yellow);\r\n    margin: 0;\r\n    letter-spacing: 1px;\r\n}\r\n\r\n@keyframes hint-bounce {\r\n    0%, 100% {\r\n        transform: translateX(-50%) translateY(0);\r\n    }\r\n    50% {\r\n        transform: translateX(-50%) translateY(5px);\r\n    }\r\n}\r\n\r\n/* --- NUCLEAR BUTTON (UI Layer) --- */\r\n#nuclear-button-container {\r\n    position: fixed;\r\n    bottom: 3rem;\r\n    right: 3rem;\r\n    z-index: 200;\r\n    opacity: 0;\r\n    transition: opacity 0.5s ease;\r\n    pointer-events: none;\r\n}\r\n\r\n#nuclear-button-container.show {\r\n    opacity: 1;\r\n    pointer-events: auto;\r\n}\r\n\r\n.nuclear-button {\r\n    position: relative;\r\n    width: 120px;\r\n    height: 120px;\r\n    border: none;\r\n    background: none;\r\n    cursor: pointer;\r\n    padding: 0;\r\n    outline: none;\r\n    transform-style: preserve-3d;\r\n    transition: transform 0.1s ease;\r\n}\r\n\r\n.nuclear-button:hover {\r\n    transform: scale(1.05);\r\n}\r\n\r\n.nuclear-button:active {\r\n    transform: scale(0.95) translateY(5px);\r\n}\r\n\r\n/* Button base () - HIDDEN as per user request */\r\n.button-base {\r\n    display: none;\r\n}\r\n\r\n/* Button top () - Now the only visible part */\r\n.button-top {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    background: radial-gradient(circle at 30% 30%, #ff3333, #cc0000);\r\n    border-radius: 50%;\r\n    box-shadow:\r\n        0 5px 15px rgba(255, 0, 0, 0.5),\r\n        0 0 20px rgba(255, 0, 0, 0.3),\r\n        inset 0 -3px 8px rgba(0, 0, 0, 0.4),\r\n        inset 0 3px 8px rgba(255, 100, 100, 0.4);\r\n    animation: nuclear-pulse 2s ease-in-out infinite;\r\n    transition: transform 0.1s ease, box-shadow 0.1s ease;\r\n}\r\n\r\n.nuclear-button:active .button-top {\r\n    transform: translateY(5px);\r\n    box-shadow:\r\n        0 2px 8px rgba(255, 0, 0, 0.3),\r\n        inset 0 -2px 5px rgba(0, 0, 0, 0.4),\r\n        inset 0 2px 5px rgba(255, 100, 100, 0.4);\r\n}\r\n\r\n@keyframes nuclear-pulse {\r\n    0%, 100% {\r\n        box-shadow:\r\n            0 5px 15px rgba(255, 0, 0, 0.5),\r\n            0 0 20px rgba(255, 0, 0, 0.3),\r\n            inset 0 -3px 8px rgba(0, 0, 0, 0.4),\r\n            inset 0 3px 8px rgba(255, 100, 100, 0.4);\r\n    }\r\n    50% {\r\n        box-shadow:\r\n            0 5px 20px rgba(255, 0, 0, 0.8),\r\n            0 0 40px rgba(255, 0, 0, 0.6),\r\n            inset 0 -3px 8px rgba(0, 0, 0, 0.4),\r\n            inset 0 3px 8px rgba(255, 100, 100, 0.4);\r\n    }\r\n}\r\n\r\n/* Button label */\r\n.button-label {\r\n    position: absolute;\r\n    bottom: -30px;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    color: #ff0000;\r\n    font-family: var(--font-main);\r\n    font-size: 0.9rem;\r\n    font-weight: bold;\r\n    letter-spacing: 3px;\r\n    text-shadow:\r\n        0 0 5px rgba(255, 0, 0, 0.8),\r\n        0 0 10px rgba(255, 0, 0, 0.4);\r\n    white-space: nowrap;\r\n}\r\n\r\n/* --- PERFORMANCE STATS --- */\r\n#performance-stats {\r\n    position: fixed;\r\n    top: 100px;\r\n    left: 2rem;\r\n    background: rgba(0, 0, 0, 0.8);\r\n    color: var(--neon-blue);\r\n    padding: 1rem;\r\n    font-size: 0.8rem;\r\n    font-family: monospace;\r\n    border: 1px solid var(--neon-blue);\r\n    border-radius: 3px;\r\n}\r\n\r\n/* --- UTILITY CLASSES --- */\r\n.hidden {\r\n    display: none !important;\r\n}\r\n\r\n.cyber-button {\r\n    background: var(--ui-bg);\r\n    color: var(--neon-pink);\r\n    border: 2px solid var(--neon-pink);\r\n    padding: 0.8rem 1.5rem;\r\n    cursor: pointer;\r\n    font-family: var(--font-main);\r\n    text-transform: uppercase;\r\n    letter-spacing: 2px;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.cyber-button:hover {\r\n    background: var(--neon-pink);\r\n    color: var(--dark-bg);\r\n    box-shadow: 0 0 20px var(--neon-pink);\r\n}\r\n\r\n/* --- RESPONSIVE DESIGN --- */\r\n@media (max-width: 768px) {\r\n    #main-nav {\r\n        width: 100%;\r\n    }\r\n\r\n    .glitch-text {\r\n        font-size: 2.5rem;\r\n    }\r\n\r\n    #instructions {\r\n        font-size: 0.75rem;\r\n        padding: 0.8rem;\r\n    }\r\n\r\n    #mini-map {\r\n        width: 150px;\r\n        height: 150px;\r\n        bottom: 1rem;\r\n        right: 1rem;\r\n    }\r\n\r\n    #scene-title {\r\n        font-size: 2rem;\r\n    }\r\n\r\n    #scene-description {\r\n        font-size: 1rem;\r\n    }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n    #menu-toggle,\r\n    .icon-button {\r\n        width: 40px;\r\n        height: 40px;\r\n    }\r\n\r\n    #mini-map {\r\n        display: none;\r\n    }\r\n\r\n    #instructions {\r\n        max-width: 200px;\r\n    }\r\n}\r\n\r\n/* --- ANIMATIONS --- */\r\n@keyframes pulse-glow {\r\n    0%, 100% {\r\n        box-shadow: 0 0 5px var(--neon-pink);\r\n    }\r\n    50% {\r\n        box-shadow: 0 0 20px var(--neon-pink);\r\n    }\r\n}\r\n\r\n@keyframes scan-line {\r\n    0% {\r\n        transform: translateY(-100%);\r\n    }\r\n    100% {\r\n        transform: translateY(100%);\r\n    }\r\n}\r\n\r\n/* Scan line effect */\r\n.scan-line {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 2px;\r\n    background: linear-gradient(transparent, var(--neon-blue), transparent);\r\n    animation: scan-line 3s linear infinite;\r\n    pointer-events: none;\r\n}\r\n\r\n/* --- INFO PANEL --- */\r\n.info-panel {\r\n    position: fixed;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%) scale(0.9);\r\n    width: 90%;\r\n    max-width: 600px;\r\n    max-height: 80vh;\r\n    background: var(--ui-bg);\r\n    border: 2px solid var(--neon-pink);\r\n    border-radius: 10px;\r\n    padding: 2rem;\r\n    backdrop-filter: blur(15px);\r\n    box-shadow: 0 0 30px rgba(255, 0, 255, 0.5);\r\n    opacity: 0;\r\n    pointer-events: none;\r\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\r\n    z-index: 1000;\r\n    overflow-y: auto;\r\n}\r\n\r\n.info-panel.show {\r\n    opacity: 1;\r\n    pointer-events: auto;\r\n    transform: translate(