ntent-title h1 {\r\n        font-size: 2rem;\r\n    }\r\n\r\n    #content-text p {\r\n        font-size: 1rem;\r\n        padding: 0.8rem 1rem;\r\n    }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n    .info-title {\r\n        font-size: 1.5rem;\r\n    }\r\n\r\n    .info-content {\r\n        font-size: 0.9rem;\r\n    }\r\n\r\n    .achievement-notification {\r\n        padding: 1rem;\r\n    }\r\n\r\n    .achievement-icon {\r\n        font-size: 2rem;\r\n    }\r\n\r\n    /* Content Display Mobile */\r\n    #content-display {\r\n        width: 95%;\r\n        bottom: 8%;\r\n    }\r\n\r\n    #content-title h1 {\r\n        font-size: 1.5rem;\r\n        letter-spacing: 1px;\r\n    }\r\n\r\n    #content-text p {\r\n        font-size: 0.9rem;\r\n        line-height: 1.6;\r\n        padding: 0.6rem 0.8rem;\r\n    }\r\n\r\n    #content-hint p {\r\n        font-size: 0.8rem;\r\n    }\r\n}\r\n\r\n/* --- NAVIGATION HINT (Initial User Guide) --- */\r\n.navigation-hint {\r\n    position: fixed;\r\n    bottom: 2rem;\r\n    left: 50%;\r\n    transform: translateX(-50%) translateY(20px);\r\n    background: var(--ui-bg);\r\n    border: 2px solid var(--neon-purple);\r\n    border-radius: 10px;\r\n    padding: 1.5rem 2.5rem;\r\n    backdrop-filter: blur(15px);\r\n    box-shadow: 0 0 40px rgba(157, 0, 255, 0.6);\r\n    text-align: center;\r\n    z-index: 1500;\r\n    opacity: 0;\r\n    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);\r\n    pointer-events: none;\r\n}\r\n\r\n.navigation-hint.show {\r\n    opacity: 1;\r\n    transform: translateX(-50%) translateY(0);\r\n}\r\n\r\n.navigation-hint.fade-out {\r\n    opacity: 0;\r\n    transform: translateX(-50%) translateY(20px);\r\n}\r\n\r\n.navigation-hint p {\r\n    color: var(--neon-blue);\r\n    font-size: 1.1rem;\r\n    line-height: 1.8;\r\n    margin: 0.5rem 0;\r\n    text-shadow: 0 0 5px var(--neon-blue);\r\n}\r\n\r\n.navigation-hint p:first-child {\r\n    color: var(--electric-yellow);\r\n    font-size: 1.3rem;\r\n    font-weight: bold;\r\n    text-shadow: 0 0 10px var(--electric-yellow);\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .navigation-hint {\r\n        width: 85%;\r\n        padding: 1.5rem 2rem;\r\n    }\r\n\r\n    .navigation-hint p {\r\n        font-size: 1rem;\r\n    }\r\n\r\n    .navigation-hint p:first-child {\r\n        font-size: 1.1rem;\r\n    }\r\n}\r\n\r\n@media (max-width: 480px) {\r\n    .navigation-hint {\r\n        width: 90%;\r\n        padding: 1rem 1.5rem;\r\n    }\r\n\r\n    .navigation-hint p {\r\n        font-size: 0.9rem;\r\n        line-height: 1.6;\r\n    }\r\n\r\n    .navigation-hint p:first-child {\r\n        font-size: 1rem;\r\n    }\r\n}\r\n"],"sourceRoot":""}]);
